  <div
    class="header-container"
    id="navbar"
    :style="[
      settingByTemplate7[0].setting7Header,
      settingByTemplate7[0].setting7General,
    ]"
    v-if="settingByTemplate7 && settingByTemplate7[0].setting7Header"
  >
    <div
      class="wrapper-header"
      id="headbg"
      :style="[
        {
          '--font-style-1':
            this.settingByTemplate7 &&
            this.settingByTemplate7[0].setting7General &&
            this.settingByTemplate7[0].setting7General.fount_1
              ? this.settingByTemplate7[0].setting7General.fount_1
              : 'David libre',
        },
        {
          '--font-style-3':
            this.settingByTemplate7 &&
            this.settingByTemplate7[0].setting7General &&
            this.settingByTemplate7[0].setting7General.fount_3
              ? this.settingByTemplate7[0].setting7General.fount_3
              : 'Lora',
        },
      ]"
    >
      <div class="header" id="headerid">
        <KoOrder :dataStore="dataStore" />
        <div class="header-item-menu" @click="openMenuLateral">
          <menu-icon class="header-icon-menu nav-bar" />
          <span class="header-text-menu">{{ $t('header_menu') }}</span>
        </div>
        <div class="header-content-logo">
          <nuxt-link to="/" class="wrapper-logo">
            <img
              :src="`${this.$store.state.urlKomercia}/logos/${dataStore.tienda.logo}`"
              class="header-logo"
              alt="Logo Img"
              @click="clear"
            />
          </nuxt-link>
        </div>
        <div class="header-content-buttons">
          <div v-for="(item, index) in secciones" :key="`${index}${item.name}`">
            <nuxt-link :to="item.path" v-if="item.path" class="btn"
              >{{ $t(`${item.name}`) }}
            </nuxt-link>
            <nuxt-link
              :to="item.href"
              v-else-if="item.href && listArticulos > 0"
              class="btn"
              >{{ $t(`${item.name}`) }}</nuxt-link
            >
          </div>
        </div>
        <div class="header-content-items">
          <div v-if="showSearch">
            <div class="search" @click="openSearch">
              <form class="header-search-icon">
                <svg
                  class="search-header"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                  />
                </svg>
              </form>
            </div>
          </div>
          <div class="empty" v-if="showSearch"></div>
          <div class="header-content-icon">
            <div class="header-content-cart" @click="openOrder">
              <svg
                class="icon-shop"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
                />
              </svg>
            </div>
            <div class="border-num-items">
              <span an class="num-items">{{ productsCart }}</span>
            </div>
          </div>
        </div>
        <KoSearch
          :dataStore="dataStore"
          :settingKProdutCard="this.settingByTemplate7[0].settingKProdutCard"
          :settingGeneral="this.settingByTemplate7[0].setting7General"
        />
        <KoMenu :dataStore="dataStore" class="responsive" />
      </div>
    </div>
  </div>
</template>

<script>
import KoOrder from '../_order1/order1'
import KoMenu from '../_lateralMenu/_lateralMenu07/openMenuLeft.vue'
import KoSearch from '../_lateralMenu/_lateralMenu07/searchUp.vue'

export default {
  components: {
    KoOrder,
    KoMenu,
    KoSearch,
  },
  name: 'Ko-Header-4',
  props: {
    dataStore: Object,
    settingByTemplate7: Array,
  },
  mounted() {
    let colorBg = ''
    if (this.settingByTemplate7 && this.settingByTemplate7[0].setting7Header) {
      colorBg =
        this.settingByTemplate7[0].setting7Header['--background_color_1']
    } else {
      colorBg = '#ffffff'
    }
    if (this.$route.fullPath == '/') {
      this.showSearch = true
    } else if (this.$route.query && this.$route.query.search) {
      this.showSearch = true
      this.setSearch(this.$route.query.search)
    } else {
      this.showSearch = false
    }
    // var prevScrollpos = window.pageYOffset
    var navbar = document.getElementById('navbar')
    var header = document.getElementById('headerid')
    var bghead = document.getElementById('headbg')
    if (
      this.settingByTemplate7 &&
      this.settingByTemplate7[0].setting7Header &&
      this.settingByTemplate7[0].setting7Header.transparente
    ) {
      header.style.backgroundColor = 'transparent'
      window.onscroll = function () {
        var currentScrollPos = window.pageYOffset
        if (currentScrollPos == 0) {
          if (screen.width >= 300) {
            header.style.width = '100%'
            bghead.style.backgroundColor = 'transparent'
            header.style.backgroundColor = 'transparent'
            bghead.style.boxShadow = 'transparent'
            header.style.boxShadow = 'transparent'
          }
          if (screen.width >= 1280) {
            header.style.width = '93%'
            navbar.style.zIndex = '20'
            navbar.style.top = '30px'
          }
        } else {
          navbar.style.top = '0px'
          navbar.style.zIndex = '20'
          bghead.style.backgroundColor = 'transparent'
          header.style.backgroundColor = 'transparent'
          header.style.boxShadow = 'transparent'
          bghead.style.boxShadow = 'transparent'
          if (screen.width >= 300) {
            header.style.width = '100%'
          }
          if (screen.width >= 1280) {
            header.style.width = '93%'
          }
        }
        // prevScrollpos = currentScrollPos
      }
    } else {
      header.style.backgroundColor = colorBg
      window.onscroll = function () {
        var currentScrollPos = window.pageYOffset
        if (currentScrollPos == 0) {
          if (screen.width >= 300) {
            header.style.width = '100%'
            bghead.style.backgroundColor = 'transparent'
            bghead.style.boxShadow = '0px 0px 0px 0px'
            header.style.boxShadow =
              '0px 0px 22px 2px rgba(145, 145, 145, 0.57)'
          }
          if (screen.width >= 1280) {
            header.style.width = '93%'
            navbar.style.zIndex = '20'
            navbar.style.top = '30px'
          }
        } else {
          navbar.style.top = '0px'
          navbar.style.zIndex = '20'
          bghead.style.backgroundColor = colorBg
          header.style.boxShadow = '0px 0px 0px 0px'
          bghead.style.boxShadow = '0px 0px 10px 4px rgba(145,145,145,0.10)'
          if (screen.width >= 300) {
            header.style.width = '100%'
          }
          if (screen.width >= 1280) {
            header.style.width = '93%'
          }
        }
        // prevScrollpos = currentScrollPos
      }
    }
  },
  data() {
    return {
      resizehead: false,
      search: '',
      showSearch: false,
      secciones: [
        {
          name: 'header_inicio',
          path: '/',
        },
        {
          name: 'header_productos',
          path: '/productos',
        },
        {
          name: 'header_contacto',
          path: '/contacto',
        },
        {
          name: 'header_blog',
          href: '/blog',
        },
      ],
    }
  },
  computed: {
    productsCart() {
      return this.$store.state.productsCart.length
    },
    facebookPixel() {
      return this.$store.state.analytics_tagmanager
    },
    listArticulos() {
      return this.$store.state.listArticulos.length
    },
  },
  methods: {
    openSearch() {
      this.$store.commit('SET_OPEN_SEARCH', true)
    },
    closedSearch() {
      this.$store.commit('SET_OPEN_SEARCH', false)
    },
    openOrder() {
      this.$store.commit('SET_OPEN_ORDER', true)
    },
    openMenuLateral() {
      this.$store.commit('SET_OPEN_ORDER_MENU_LEFT', true)
    },
    closed() {
      this.$store.commit('SET_OPEN_ORDER_MENU_LEFT', false)
    },
    clear() {
      this.$router.push({
        path: '/',
      })
      this.$store.commit('SET_STATE_BANNER', true)
    },
    SearchProduct(search) {
      this.$store.commit('SET_SEARCH_VALUE', search)
    },
    getSearch(value) {
      if (value) {
        location.href = '?search=' + value
        if (this.facebookPixel && this.facebookPixel.pixel_facebook != null) {
          window.fbq('track', 'Search', { search_string: value })
        }
      } else {
        location.href = '?search=' + ''
      }
    },
    setSearch(value) {
      this.openSearch()
      this.search = decodeURIComponent(value)
    },
    focusInput() {
      document.getElementById('SearchHeader').focus()
    },
  },
  watch: {
    search(value) {
      this.SearchProduct(value)
    },
    // eslint-disable-next-line no-unused-vars
    $route(to, from) {
      if (this.$route.fullPath == '/') {
        this.showSearch = true
      } else if (this.$route.query && this.$route.query.search) {
        this.showSearch = true
        this.setSearch(this.$route.query.search)
      } else {
        this.showSearch = false
      }
    },
  },
}
</script>

